.page {
  font-family: "Roboto", sans-serif;
}

.page__content,
.card__title {
  margin: 0;
}

.card {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  padding: 32px 16px 16px;
  width: 200px;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
} /*main class for card*/

.card__image {
  height: 134px;
  width: 160px;
  margin: 0 auto 40px;
}

.card__title {
  margin-bottom: 4px;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #060b35;
}

.card__code {
  margin: 0 0 16px;
  color: #616070;
}

.card__code,
.card__reviews {
  font-weight: 400;
  font-size: 10px;
  line-height: 14px;
}

.card__reviews {
  align-items: center;
  margin-bottom: 24px;
  color: #060b35;
}

.card__price {
  margin: 0 0 16px;
  font-size: 12px;
  line-height: 18px;
}

.card__price,
.card__reviews {
  display: flex;
  justify-content: space-between;
}

.card__price-value {
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  color: #060b35;
}

.card__button {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  background-color: #00acdc;
  font-weight: 700;
  font-size: 14px;
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  border: 1px solid transparent;
  border-radius: 5px;
}

.card__button:hover {
  background: #fff;
  border-color: #00acdc;
  color: #00acdc;
}

.stars {
  display: flex;
} /*main class for block stars*/

.stars__star {
  height: 16px;
  width: 16px;
  margin-right: 4px;
  background-image: url(./images/star.svg);
  background-repeat: no-repeat;
  background-position: 50%;
}

.stars--1 :nth-child(-n+1),
.stars--2 :nth-child(-n+2),
.stars--3 :nth-child(-n+3),
.stars--4 :nth-child(-n+4),
.stars--5 :nth-child(-n+5) {
  background-image: url(./images/star-active.svg);
}
